body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-radial-gradient(#ffffff, #eee);
  background: -o-radial-gradient(#ffffff, #eee);
  background: radial-gradient(#ffffff, #eee);
}
/* https://paulrhayes.com/animated-css3-cube-interface-using-3d-transforms/ */
#cube-container {
  -webkit-perspective: 800px;
          perspective: 800px;
  -webkit-perspective-origin: 50% 200px;
          perspective-origin: 50% 200px;
  margin: 250px auto;
  outline: none;
}

#cube {
  position: relative;
  margin: auto;
  height: 400px;
  width: 400px;
  -webkit-transition: -webkit-transform 0.3s linear;
  transition: -webkit-transform 0.3s linear;
  -o-transition: transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  /* animation: spin 6s ease-in-out infinite; */
}

#scramble-button {
  position: absolute;
  cursor: pointer;
  margin: 0 200px;
}

.face {
  display: contents;
}

.tile {
  position: absolute;
  margin: 1px;
  border: 4px solid black;
  border-radius: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 105px;
  height: 105px;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.arrow {
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  width: 50px;
  position: absolute;
  opacity: 0.1;
  cursor: pointer;
}

.arrow:hover {
  opacity: 0.9;
}

.LM-left {
  -webkit-transform: scaleX(-1) translate(0, -60px) rotate(-110deg);
      -ms-transform: scaleX(-1) translate(0, -60px) rotate(-110deg);
          transform: scaleX(-1) translate(0, -60px) rotate(-110deg);
}

.CM-left {
  -webkit-transform: scaleX(-1) translate(-80px, -45px) rotate(-110deg);
      -ms-transform: scaleX(-1) translate(-80px, -45px) rotate(-110deg);
          transform: scaleX(-1) translate(-80px, -45px) rotate(-110deg);
}

.RM-left {
  -webkit-transform: scaleX(-1) translate(-160px, -20px) rotate(-110deg);
      -ms-transform: scaleX(-1) translate(-160px, -20px) rotate(-110deg);
          transform: scaleX(-1) translate(-160px, -20px) rotate(-110deg);
}

.LM-right {
  -webkit-transform: translate(-220px, 325px) rotate(-230deg);
      -ms-transform: translate(-220px, 325px) rotate(-230deg);
          transform: translate(-220px, 325px) rotate(-230deg);
}

.CM-right {
  -webkit-transform: translate(-160.5px, 370px) rotate(-230deg);
      -ms-transform: translate(-160.5px, 370px) rotate(-230deg);
          transform: translate(-160.5px, 370px) rotate(-230deg);
}

.RM-right {
  -webkit-transform: translate(-90px, 425px) rotate(-230deg);
      -ms-transform: translate(-90px, 425px) rotate(-230deg);
          transform: translate(-90px, 425px) rotate(-230deg);
}

.UE-left {
  -webkit-transform: scaleX(-1) translate(270px, 90px) rotate(0deg);
      -ms-transform: scaleX(-1) translate(270px, 90px) rotate(0deg);
          transform: scaleX(-1) translate(270px, 90px) rotate(0deg);
}

.UE-right {
  -webkit-transform: translate(220px, 85px) rotate(0deg);
      -ms-transform: translate(220px, 85px) rotate(0deg);
          transform: translate(220px, 85px) rotate(0deg);
}

.CE-right {
  -webkit-transform: translate(210px, 185px) rotate(0deg);
      -ms-transform: translate(210px, 185px) rotate(0deg);
          transform: translate(210px, 185px) rotate(0deg);
}

.CE-left {
  -webkit-transform: scaleX(-1) translate(260px, 180px) rotate(0deg);
      -ms-transform: scaleX(-1) translate(260px, 180px) rotate(0deg);
          transform: scaleX(-1) translate(260px, 180px) rotate(0deg);
}

.DE-left {
  -webkit-transform: scaleX(-1) translate(250px, 265px) rotate(0deg);
      -ms-transform: scaleX(-1) translate(250px, 265px) rotate(0deg);
          transform: scaleX(-1) translate(250px, 265px) rotate(0deg);
}

.DE-right {
  -webkit-transform: translate(200px, 265px) rotate(0deg);
      -ms-transform: translate(200px, 265px) rotate(0deg);
          transform: translate(200px, 265px) rotate(0deg);
}



